const FolderWrapper = styled.div`
    display: flex;
    justify-content: center;
`;

const StyledFolderContainer = styled.div`
    margin-top: 5px;
    position: relative;
`;

const StyledFolderTopCurve = styled.svg`
    position: absolute;
    fill: rgba(53, 67, 98, 1);
    right: -40px;
    width: 40px;
    height: 20px;
`;

const Message = styled.p`
    @import url('https://fonts.googleapis.com/css2?family=Lilita+One&family=Raleway+Dots&display=swap');
    font-family: 'Lilita One', cursive;
    font-family: 'Raleway Dots', cursive;
    position: absolute;
    color: white;
    padding: 20px;
`;

const StyledFolderTop = styled.div`
    position: relative;
    width: 100px;
    height: 20px;
    background: rgba(53, 67, 98, 1);
    border-top-left-radius: 20px;
`;

const StyledFolder = styled.div`
    display: flex;
    align-items: center;
    width: 384px;
    height: 217px;
    background: rgba(53, 67, 98, 1);
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    border-bottom-left-radius: 20px;
    margin-bottom: 20px;
`;

const OtherWrapper = styled.div`
    display: flex;
    justify-content: center;
`;

const StyledButton = styled.button`
    margin-top: 20px;
    background: #36c6d9;
    border-radius: 24px;
    border: none;
    outline: none;
    width: 312px;
    height: 48px;

    text-align: center;
    font-weight: 400;

    font-family: 'Pangolin', sans-serif;
`;

export const StyledSeconds = styled.div`
    margin-top: 20px;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    letter-spacing: 0.002em;
    color: rgba(255, 255, 255, 0.4);
`;

const StyledError = styled.div`
    margin-top: 13px;
    font-size: 12px;
    line-height: 16px;
    text-align: center;
    letter-spacing: 0.004em;
    color: #ef6c65;
`;

const StyledReactInputVerificationCode = styled.div`
    display: flex;
    justify-content: center;

    --ReactInputVerificationCode-itemWidth: 40px;
    --ReactInputVerificationCode-itemHeight: 48px;
    --ReactInputVerificationCode-itemSpacing: 8px;

    .ReactInputVerificationCode__item {
        font-size: 16px;
        font-weight: 500;
        color: #fff;

        background: rgba(53, 67, 98, 1);
        border: 1px solid ${({ isInvalid }) => (isInvalid ? '#EF6C65' : 'rgba(28, 30, 60, 0.4)')};
        border-radius: 4px;
        box-shadow: none;
    }

    .ReactInputVerificationCode__item.is-active {
        box-shadow: none;
        border: 1px solid #36c6d9;
    }
`;

// ===============================|| UI COLOR ||=============================== //

const EmailVerification = () => {
    const theme = useTheme();

    const [value, setValue] = useState('');
    const [isInvalid, setIsInvalid] = useState(false);
    const [error, setError] = useState(null);

    const [seconds, setSeconds] = useState(null);

    return (
        <Container>
            <FolderWrapper>
                <StyledFolderContainer>
                    <StyledFolderTop>
                        <StyledFolderTopCurve viewBox="0 0 40 20">
                            <path
                                d="
          M 0,0
          Q 10,0 20,10
          T 40,20
          0,20
        "
                            />
                        </StyledFolderTopCurve>
                    </StyledFolderTop>
                    <StyledFolder>
                        <Message>You have to verify your email. Check your Email Inbox for a six verification code.</Message>
                    </StyledFolder>
                </StyledFolderContainer>
            </FolderWrapper>

            <StyledReactInputVerificationCode isInvalid={isInvalid}>
                <ReactInputVerificationCode
                    value={value}
                    placeholder={null}
                    length={6}
                    onChange={(newValue) => {
                        setValue(newValue);

                        if (newValue !== '') {
                            setError(null);
                        }
                    }}
                />
            </StyledReactInputVerificationCode>

            <OtherWrapper>{error && <StyledError>{error}</StyledError>}</OtherWrapper>
            <OtherWrapper>{seconds && <StyledSeconds>{`Verification code has been re-sent (${seconds}s)`}</StyledSeconds>}</OtherWrapper>
            <OtherWrapper>
                <StyledButton
                    onClick={() => {
                        setValue('');
                        setError('Incorrect code. Please try again');
                        setIsInvalid(true);
                        setSeconds(60);

                        let mySeconds = 60;

                        // TODO Clear previos interval

                        const intervalId = setInterval(() => {
                            mySeconds -= 1;
                            setSeconds(mySeconds);

                            if (mySeconds === 0) {
                                clearInterval(intervalId);
                                setSeconds(null);
                            }
                        }, 1000);

                        setTimeout(() => {
                            setIsInvalid(false);
                        }, 1000);
                    }}
                >
                    Verify
                </StyledButton>
            </OtherWrapper>
        </Container>
    );
};

export default EmailVerification;
